	module.exports = {"data":{"markdownRemark":{"html":"<h2>What is an a/b test?</h2>\n<p>An a/b test is a method of comparing two versions of a webpage or app against each other to determine which one performs better. ab testing uses data and statistics to validate new design changes and improve conversion rates.</p>\n<p>A/B testing allows individuals, teams, and companies to make careful changes to their user experiences while collecting data on the results. This allows them to construct hypotheses and to better learn why certain elements of their experiences impact user behavior.</p>\n<h2>How to conduct an a/b test:</h2>\n<p>A minimum of 20-25 users/interactions is recommended per iteration. The more the better!</p>\n<p>If you are testing in person, prepare a script and always use the same script to keep the tests consistent. Test with one participant at a time to remove bias from questions and comparisons. ask the participant to think out loud so you will better understand their thought process. ask the participant why they selected a certain version.</p>\n<p>You can also use a platform like <a href=\"https://usabilityhub.com/\">Usability Hub</a> to conduct a/b tests.</p>\n<p>Compare qualitative and quantitative data to determine which version performs better, then use the results to iterate on your product.</p>","frontmatter":{"title":"A/B Test","overview":[{"heading":"Investment","amount":2,"description":"An a/b test intentionally measures only a single change between two otherwise similar items. The change can be as simple as altering the copy on a button and measuring which is clicked more. analytics software can be used to facilitate the recording and comparison process."},{"heading":"Validity","amount":5,"description":"Running an A/B test with a significant sample size returns quantitative results that are nearly impossible to argue with."},{"heading":"Expertise","amount":3,"description":"Implementing an a/b test can require some technical know-how depending on the process employed. A general understanding of statistics will help analyze results, but is not required."}],"resources":null,"relatedMethods":[{"text":"Usability Test","url":"/usability-testing/"}]},"fields":{"slug":"/ab-test/"}},"allMarkdownRemark":{"edges":[{"node":{"frontmatter":{"title":"Usability Testing","url":"/usability-testing/"}}},{"node":{"frontmatter":{"title":"User Research Guidelines","url":null}}},{"node":{"frontmatter":{"title":"Affinity Diagram","url":"/affinity-diagram/"}}},{"node":{"frontmatter":{"title":"A/B Test","url":"/ab-test/"}}},{"node":{"frontmatter":{"title":"Card Sort","url":"/card-sort/"}}},{"node":{"frontmatter":{"title":"Empathy Map","url":"/empathy-map/"}}},{"node":{"frontmatter":{"title":"Focus Group","url":"/focus-group/"}}},{"node":{"frontmatter":{"title":"Interview","url":"/interview/"}}},{"node":{"frontmatter":{"title":"Journey Map","url":"/journey-map/"}}},{"node":{"frontmatter":{"title":"Participatory Design","url":"/participatory-design/"}}},{"node":{"frontmatter":{"title":"Persona","url":"/persona/"}}}]}},"pathContext":{"slug":"/ab-test/"}}

